/** @jsx jsx */
import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { jsx, useThemeUI } from "theme-ui"
import { useShopProducts } from "Project/_static-queries/shop-products"
import getPaymentUrl from "Components/payments/qiwi"
import Layout from "Project/_components/layouts/001"
import PageCover from "Project/_components/pageCover"
import { CheckSquareFill } from "react-bootstrap-icons"

export default function IndexPage() {
  const context = useThemeUI()
  const { theme } = context
  const shopProductsData = useShopProducts()
  let clientEmail = ""
  let byProductAmount = 1

  return (
    <Layout>
      <PageCover text={"Аккаунты World of Tanks"}>
        <StaticQuery
          query={graphql`
            {
              file(relativePath: { eq: "_assets/covers/cover-001.jpg" }) {
                childImageSharp {
                  fixed(width: 1600) {
                    ...GatsbyImageSharpFixed_withWebp
                  }
                }
              }
            }
          `}
          render={data => (
            <Img
              fixed={data.file.childImageSharp.fixed}
              style={{
                display: "flex",
                minHeight: "240px",
                width: "100%",
                position: "absolute !important",
              }}
            />
          )}
        ></StaticQuery>
      </PageCover>
      <div className={"product-list"} sx={{ padding: "16px" }}>
        <h2
          sx={{
            marginBottom: "16px",
            "@media (max-width: 768px)": {
              paddingX: "16px",
            },
          }}
        >
          Личные аккаунты
        </h2>
        <div
          className="table-responsive"
          sx={{
            table: {
              thead: { userSelect: "none" },
              tbody: {
                boxShadow: `inset 1px 0px 0px ${theme.colors.separator_opaque},inset -1px 0px 0px ${theme.colors.separator_opaque},inset 0px 1px 0px ${theme.colors.separator_opaque},inset 0px -1px 0px ${theme.colors.separator_opaque}`,
                tr: {
                  ":nth-of-type(odd)": {
                    backgroundColor: "fill_quarternary",
                  },
                },
              },
              th: {
                paddingX: "16px",
                color: "text_primary",
                textTransform: "uppercase",
                display: "table-cell",
                fontWeight: "normal",
                borderBottomWidth: "0px",
                minHeight: "32px",
                whiteSpace: "nowrap",
              },
              td: {
                paddingX: "16px",
                display: "table-cell",
                color: "text_secondary",
                borderBottomWidth: "0px",
              },
              tr: {
                ":hover": {
                  ".table-buy-button": {
                    color: "white",
                    backgroundColor: "tint_blue",
                  },
                },
              },
            },
            "@media (max-width: 767.98px)": {
              scrollbarWidth: "none",
              msOverflowStyle: "none",
              "::-webkit-scrollbar": {
                display: "none",
              },
              table: {
                width: "768px",
                tbody: {
                  tr: {
                    ".table-buy-button": {
                      color: "white",
                      backgroundColor: "tint_blue",
                    },
                  },
                },
              },
            },
          }}
        >
          <table className="table">
            <thead>
              <tr>
                <th className={"element-small"} scope="col">
                  Товар
                </th>
                <th className={"element-small"} scope="col">
                  Наличие
                </th>
                <th className={"element-small"} colSpan="2">
                  Цена за 1 шт.
                </th>
              </tr>
            </thead>
            <tbody>
              {shopProductsData.map(
                ({ node }) =>
                  node.frontmatter.categories.indexOf("randomAccount") !==
                    -1 && (
                    <tr key={node.id}>
                      <td className={"element-medium"} scope="col">
                        {node.frontmatter.extra.link !== null && (
                          <a
                            href={node.frontmatter.extra.link}
                            target={"blank"}
                            sx={{ color: "tint_blue", marginRight: "8px" }}
                          >
                            Ссылка на аккаунт.
                          </a>
                        )}
                        <Link to={node.slug} sx={{ color: "text_secondary" }}>
                          {node.excerpt}
                        </Link>
                      </td>
                      <td className={"element-medium"} scope="col">
                        {node.frontmatter.extra.amount}
                      </td>
                      <td
                        className={"element-medium"}
                        scope="col"
                        sx={{
                          paddingRight: "0px !important",
                          paddingY: "0px !important",
                        }}
                      >
                        <div
                          sx={{
                            display: "grid",
                            gridAutoFlow: "column",
                            justifyContent: "space-between",
                            alignItems: "center",
                          }}
                        >
                          <span>{node.frontmatter.extra.price}</span>
                          <button
                            data-toggle="modal"
                            data-target={`#${node.id.replace(
                              /[.,"*+!-?^${}()|[\]\\/]/gu,
                              ""
                            )}`}
                            className={
                              "element-medium element-button table-buy-button"
                            }
                            sx={{
                              color: "tint_blue",
                              marginLeft: "8px",
                              paddingX: "16px",
                            }}
                          >
                            Купить
                          </button>
                        </div>
                        <div
                          className="modal fade"
                          id={node.id.replace(/[.,"*+!-?^${}()|[\]\\/]/gu, "")}
                          tabIndex="-1"
                          aria-hidden="true"
                        >
                          <div className="modal-dialog modal-dialog-centered modal-sm">
                            <div className="modal-content">
                              <form
                                sx={{
                                  backgroundColor: "background_primary",
                                  borderRadius: `${theme.root.secondaryBorderRadius}`,
                                }}
                                onSubmit={event => {
                                  event.preventDefault()
                                  if (clientEmail.indexOf("@") !== -1) {
                                    const paymentInfo = {
                                      amount:
                                        node.frontmatter.extra.price *
                                        byProductAmount,
                                      email: clientEmail,
                                      comment: ``,
                                    }
                                    location.href = getPaymentUrl(paymentInfo)
                                  }
                                }}
                              >
                                <div
                                  className={"element element-large"}
                                  sx={{
                                    boxShadow: `inset 0px -1px 0px ${theme.colors.separator_opaque}`,
                                  }}
                                >
                                  <div className={"element-center-part"}>
                                    <span>Оформить заказ</span>
                                  </div>
                                </div>
                                <div
                                  sx={{
                                    paddingY: "24px",
                                    display: "flex",
                                    flexDirection: "column",
                                  }}
                                >
                                  <div className={`element element-medium`}>
                                    <div className={"element-left-part"}>
                                      <input
                                        type="email"
                                        placeholder="Ваш email"
                                        className={`element element-medium element-input input-filled`}
                                        sx={{ paddingX: "16px" }}
                                        required
                                        onChange={event => {
                                          clientEmail = event.target.value
                                        }}
                                      />
                                    </div>
                                  </div>
                                  {node.frontmatter.extra.amount > 1 && (
                                    <div
                                      className={`element element-medium`}
                                      sx={{ marginTop: "8px" }}
                                    >
                                      <div className={"element-left-part"}>
                                        <input
                                          placeholder={`Количество товара (макс. ${node.frontmatter.extra.amount})`}
                                          className={`element element-medium element-input input-filled`}
                                          sx={{ paddingX: "16px" }}
                                          required
                                          onChange={event => {
                                            byProductAmount = event.target.value
                                          }}
                                        />
                                      </div>
                                    </div>
                                  )}
                                  <div sx={{ marginTop: "24px" }}>
                                    <div
                                      className={"element element-small"}
                                      sx={{
                                        textTransform: "uppercase",
                                        color: "text_secondary",
                                        boxShadow: `0px 1px 0px ${theme.colors.separator_opaque}`,
                                      }}
                                    >
                                      <div className={"element-left-part"}>
                                        <span>Способ оплаты</span>
                                      </div>
                                    </div>

                                    <div className={`element element-medium`}>
                                      <div
                                        className={"element-left-part"}
                                        sx={{
                                          color: "text_primary",
                                          i: {
                                            marginRight: "8px !important",
                                          },
                                        }}
                                      >
                                        <i>
                                          <CheckSquareFill
                                            size={18}
                                            sx={{ color: "tint_blue" }}
                                          />
                                        </i>
                                        <span>Банковская карта или Qiwi</span>
                                      </div>
                                    </div>
                                  </div>
                                  <div sx={{ marginTop: "24px" }}>
                                    <div
                                      className={"element element-small"}
                                      sx={{
                                        color: "text_secondary",
                                        boxShadow: `0px -1px 0px ${theme.colors.separator_opaque}`,
                                        paddingY: "8px",
                                        paddingX: "16px",
                                      }}
                                    >
                                      <span>
                                        Нажимая на кнопку "Оплатить" вы
                                        соглашаетесь с{" "}
                                        <a href={"/rules"} target={"blank"}>
                                          правилами магазина
                                        </a>
                                      </span>
                                    </div>
                                  </div>
                                </div>
                                <div
                                  sx={{
                                    display: "flex",

                                    boxShadow: `inset 0px 1px 0px ${theme.colors.separator_opaque}`,
                                  }}
                                >
                                  <button
                                    type="button"
                                    data-dismiss="modal"
                                    aria-label="Close"
                                    className={
                                      "element element-large element-button"
                                    }
                                    sx={{
                                      width: "100%",
                                      justifyContent: "center",
                                      color: "tint_blue",
                                      paddingX: "16px",
                                      boxShadow: `inset -1px 0px 0px ${theme.colors.separator_opaque}`,
                                    }}
                                  >
                                    Отмена
                                  </button>
                                  <button
                                    type={"submit"}
                                    className={
                                      "element element-large element-button"
                                    }
                                    sx={{
                                      width: "100%",
                                      justifyContent: "center",
                                      paddingX: "16px",
                                      color: "tint_blue",
                                      fontWeight: "bold",
                                      ":hover": {
                                        backgroundColor: "transparent",
                                      },
                                    }}
                                  >
                                    Оплатить
                                  </button>
                                </div>
                              </form>
                            </div>
                          </div>
                        </div>
                      </td>
                    </tr>
                  )
              )}
            </tbody>
          </table>
        </div>
      </div>
    </Layout>
  )
}
